.stars {
    display: flex;
  
    &__star {
      background-image: url(/src/images/star.svg);
      background-repeat: no-repeat;
      background-position: 50%;
      margin-right: 4px;
      width: 16px;
      height: 16px;
    }
  
    @for $activeStar from 1 through 4 {
      &__star:nth-child(-n + #{$activeStar}) {
        background-image: url(/src/images/star-active.svg);
      }
    }
  }